define([
    'requireExternal',
    '../Services/MessageBusService',
    '../Navigation/Nav',
    'immutable',
    'moment',
    'hammerjs',
    '../Analytics/AdobeDTMService'
], function(requirejs, messageBus, nav, immutable, moment, hammer, dtm) {

    return {
        init: function(baseUrl, tabName) {

            require.ensure([], function() {
                var rx = require('rx'); 
                var react = require('react');
                var reactDom = require('react-dom');
                var rest = require('rwwa-rest');
                var stateMachine = require('rwwa-rx-state-machine');
                var authIntents = require('../../AppUtils/Framework/Intent/authentication');
                var menuIntents = require('../../AppUtils/Framework/Intent/menu');
                var navIntents = require('../../AppUtils/Framework/Intent/navigation');

                var container = document.getElementById('account-activity-container');

                var externalComponentRequire = requirejs.config({
                    context: 'externalRequire',
                    waitSeconds: 15
                });

                nav.showLoading();

                externalComponentRequire([baseUrl + "/static/react-client.js"],
                    function(accountActivity) {
                        nav.hideLoading();

                        var config = {
                            host: baseUrl,
                            initalTab: accountActivity.tabs[tabName]
                        }

                        var dependencies = {
                            react: react,
                            'react-dom': reactDom,
                            rx: Rx,
                            rest: rest.default,
                            immutable: immutable,
                            moment: moment,
                            hammerjs: hammer,
                            'state-machine': stateMachine,
                            'host-intents': {
                                auth: authIntents,
                                nav: navIntents,
                                menu: menuIntents,
                            },
                            analytics: { track: function(key) { dtm.trackActivity(key); } }
                        }

                        accountActivity.run(container, config, dependencies);

                        function onNav() {
                            messageBus.unsubscribe('nav.nav', onNav);
                            messageBus.unsubscribe('nav.resetback', onNav);
                            reactDom.unmountComponentAtNode(container);
                        }

                        messageBus.subscribe('nav.nav', onNav);
                        messageBus.subscribe('nav.resetback', onNav);                    
                    },

                    function(err) {
                        var failedId = err.requireModules && err.requireModules[0];
                        requirejs.undef(failedId);

                        nav.ajaxError(0, null, true);
                    });

            }, 'extcomponents'); 
        }
    }
});


