﻿define([
    'jquery'
], function (jQuery) {
    return {
        initView: initView
    }

    function initView() {
        jQuery('body#home').removeAttr('id');
        jQuery('body').removeAttr('class');
        jQuery('html').attr('class', 'page-tabtrack');
        jQuery('.button.help').hide();
        jQuery('a#backButton').show().attr('href', '/#');
    }
});


