var tabtouch = require("./Foundation/TabTouch");
var router = require("./Foundation/Router");
var raceBettingPageHandler = require("./Foundation/RaceBettingPageRouteHandler");
var nav = require("./Foundation/Navigation/Nav");
var common = require("./Foundation/Common");
var logon = require("./Foundation/Logon");
var legacy = require("./include-legacy");
var rightMenu = require("./Foundation/ViewModels/PrimaryMenuViewModel-Extension");
var rightMenuViewModel = require("./Foundation/ViewModels/PrimaryMenuViewModel");
var featureService = require("./Foundation/Services/FeatureService");
var msgBusService = require("./Foundation/Services/MessageBusService");
var accountMonitoringService = require("./Foundation/Services/AccountMonitoringService");
var deviceIdentificationService = require("./Foundation/Services/Device/DeviceIdentificationService");
var analyticsService = require("./Foundation/Analytics/AnalyticsService");
var jockeyJsMessageService = require("./Foundation/JockeyJS/JockeyJSMessageService");
var checkInService = require("./Foundation/Services/Location/CheckInService");
var locationProvider = require("./Foundation/Services/Location/LocationProvider");
var modal = require("./Foundation/Modal/Modal");
var appDyn = require("./Foundation/AppDynamics");
var analyticsDataLayer = require("./Foundation/Analytics/AnalyticsDataLayer");
var errorController = require("./Foundation/Error");
var inlineRequireDef = require("./Foundation/InlineRequireDefinitions");
var bettingInlineRequireDef = require("./Betting-v1/InlineRequireDefinitions");
var cookies = require("js-cookie");
var cms = require("./Foundation/ContentManagementSystem");

function defer(func) {
    setTimeout(func, 0 );
}

ko.options.deferUpdates = true;

ko.onError = function (error) {
    console.error("Knockout Error: \n" + error.message);
    throw error;
}

window.AnalyticsDataLayer = analyticsDataLayer;

defer( function() {
    rightMenu.init();    

    defer( function() {
        ko.applyBindings(rightMenuViewModel, document.getElementById('settings'));
        ko.applyBindings({}, document.getElementById('myBetsAndOffers'));

        rightMenuViewModel.initView();
    });
});

defer( function() {
    setupLoginRefresh();
    setupJockeyJs();
});

defer(function() {
    checkFeatures();
});

defer(function() {
    modal.init();
});

defer(function() {
    errorController.init();
    tabtouch.init();
});

nav.setRaceBettingPageHandler(raceBettingPageHandler.handle);

//Hook in Google Analytics
msgBusService.subscribe("nav.content.shown", function(url) {
    //Google Analytics
    if (window._gaq != null) {
        window._gaq.push(['_trackPageview', url]);
    }
});

//Init app dynamics
appDyn.init();

//Init nav
nav.init();
nav.initHomeUrl(window.tabtouchhomeurl);

//Default route
router.addRoute("^.*$", "^.*$", nav.nav);

//Start navigation
router.follow(window.location.hash);

//Preload BPR bundle           
require.ensure([], function() {
    require('./include-betting-v2');
    console.log("betting-v2 has been preloaded");
},'betting-v2');

//Hide URL bar
defer(common.hideURLbar);

if (cookies.get('ShowDepositModal')) {
    cookies.remove('ShowDepositModal', { path: '/' });
    ModalService.deposit();
}

function setupJockeyJs() {
    jockeyJsMessageService.start();
}

function setupLoginRefresh() {
    msgBusService.subscribe('lo.login', function () {
        featureService.clear();
        checkFeatures();
    });
    msgBusService.subscribe('lo.logout', function () {
        featureService.clear();
        checkFeatures();
    });
};

function checkFeatures() {
    featureService.isActive("Analytics")
        .then(function (active) {
            if (active) {
                analyticsService.start();
            } else {
                analyticsService.stop();
            }
        });
    featureService.isActive("DynamicCashBetting")
        .then(function (active) {
            if (active) {
                msgBusService.publish('DynamicCashBetting.On');
            } else {
                msgBusService.publish('DynamicCashBetting.Off');
            }
        });
    featureService.isActive("LocationBasedServices")
        .then(function (active) {
            if (active) {
                var checkedInLocationId = checkInService.checkedInLocationId();
                if (checkedInLocationId !== '') {
                    locationProvider.start();
                } else {
                    locationProvider.stop();
                }
            } else {
                locationProvider.stop();
            }
        });
}

//TEMP - iOS app expects LO to be global (no other code expects it so safe to remove once iOS app is updated)
window.LO = logon;



